@font-face {
    font-family: IRANSans;
    src: url('IRANSansWeb(FaNum).eot');
    src: url('IRANSansWeb(FaNum).eot?#iefix') format('embedded-opentype'),
    url('IRANSansWeb(FaNum).woff') format('woff'),
    url('IRANSansWeb(FaNum).ttf') format('truetype');
}

@font-face {
    font-family: Vazir;
    src: url('Vazir-Light-FD-WOL.eot');
    src: url('Vazir-Light-FD-WOL.eot?#iefix') format('embedded-opentype'),
    url('Vazir-Light-FD-WOL.woff') format('woff'),
    url('Vazir-Light-FD-WOL.ttf') format('truetype');
}